//@flow
import React, { Component } from "react";
import {
    Menu,
    Icon,
    Avatar,
    Dropdown,
    Modal,
    Button,
    Input,
    Form,
    message
} from "antd";
import styles from "./index.css";
import { View } from "react-web-dom";
import types from "../../../constants";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import HeaderBreadcrumb from "../../headerBreadcrumb";
import Fetch from "../../../utils/fetch";
import { MemberApi } from "../../../config/api/member";

const FormItem = Form.Item;

type Props = {
    history: {
        location: {
            pathname: string
        },
        push: Function
    },
    dispatch: Function,
    breadcrumbNameMap: {},
    routerData: {},
    routes: [],
    location: { pathname: string },
    userInfo: {
        avatar: string,
        nickname: string,
        username: string
    }
};

type State = {
    passwordEditShow: boolean
};

@withRouter
@connect(({ app: { member: { userInfo } } }) => ({ userInfo }))
export default class LayoutHeader extends Component<Props, State> {
    static defaultProps = {
        dispatch: () => {
        },
        history: {
            location: {
                pathname: ''
            },
            push: () => {
            },
        },
        userInfo: {
            avatar: '',
            nickname: '',
            username: '',
        }
    }
    state = {
        passwordEditShow: false
    };

    getDefaultCollapsedBreadcrumb(props: Props) {
        const { location: { pathname } } = props.history || this.props.history;
        let snippets = pathname.split("/");
        snippets.shift();
        snippets = snippets.map((item, index) => {
            if (index > 0) {
                return snippets.slice(0, index + 1).join("/");
            }
            return item;
        });
        snippets.shift();
        return snippets;
    }

    render() {
        const {
            dispatch,
            userInfo,
            routes,
            breadcrumbNameMap,
            location
        } = this.props;
        const { passwordEditShow } = this.state;
        return (
            <View className={styles.header}>
                <View className={styles.breadTitle}>
                    <HeaderBreadcrumb routes={routes} location={location} breadcrumbNameMap={breadcrumbNameMap}
                                      tabActiveKey={location.pathname} />
                </View>
                <Dropdown
                    overlay={
                        <Menu
                            onClick={({ key }) => {
                                switch (key) {
                                    case "userInfo":
                                        return this.props.history.push(
                                            "/member/self"
                                        );
                                    case "passwordEdit":
                                        return this.setState({
                                            passwordEditShow: true
                                        });
                                    case "logout":
                                        return dispatch({
                                            type: types.member.USER_SIGN_OUT
                                        });
                                    default:
                                }
                            }}
                        >
                            {/*<Menu.Item key="userInfo">*/}
                                {/*<span></span>*/}
                            {/*</Menu.Item>*/}
                            <Menu.Item key="passwordEdit">
                                <span></span>
                            </Menu.Item>
                            <Menu.Item key="logout">
                                <span></span>
                            </Menu.Item>
                        </Menu>
                    }
                >
                    <View className={styles.avatarName}>
                        {/*<Avatar*/}
                            {/*style={{*/}
                                {/*color: "#f56a00",*/}
                                {/*backgroundColor: "#fde3cf"*/}
                            {/*}}*/}
                            {/*src={userInfo.avatar}*/}
                        {/*>*/}
                            {/*{userInfo.nickname}*/}
                        {/*</Avatar>*/}
                        <span>{userInfo.username}</span>
                        <Icon type="down" />
                    </View>
                </Dropdown>
                <Modal
                    title=""
                    visible={passwordEditShow}
                    footer={null}
                    onCancel={() => {
                        this.setState({ passwordEditShow: false });
                    }}
                >
                    <EditPasswordForm
                        {...this.props}
                        close={() => {
                            this.setState({ passwordEditShow: false });
                        }}
                    />
                </Modal>
            </View>
        );
    }
}

@Form.create()
class EditPasswordForm extends Component<{
    form: {
        validateFieldsAndScroll: Function,
        getFieldValue: Function,
        validateFields: Function,
        getFieldDecorator: Function
    },
    close: Function
},
    {
        confirmDirty: boolean
    }> {
    static defaultProps = {
        form: {
            validateFieldsAndScroll: () => {
            },
            getFieldValue: () => {
            },
            validateFields: () => {
            },
            getFieldDecorator: () => {
            },
        }
    }
    state = {
        confirmDirty: false
    };
    handleSubmit = async (e) => {
        e.preventDefault();
        this.props.form.validateFieldsAndScroll(async (err, values) => {
            if (!err) {
                const editResult = await Fetch.fetch({
                    api: MemberApi.selfPassword,
                    params: {
                        password: values.password,
                        oldpassword: values.oldpassword,
                    }
                })
                if (editResult.code === 0) {
                    message.success("")
                } else {
                    message.warning(editResult.msg)
                }
                this.props.close()
            }
        })
    }
    handleConfirmBlur = e => {
        const value = e.target.value;
        this.setState({ confirmDirty: this.state.confirmDirty || !!value });
    }
    checkPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value && value === form.getFieldValue("oldpassword")) {
            callback("");
        } else {
            callback();
        }
    };
    render() {
        const { form, close } = this.props;
        const { getFieldDecorator } = form;
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 6 }
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 }
            }
        };
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0
                },
                sm: {
                    span: 16,
                    offset: 8
                }
            }
        };
        return (
            <Form onSubmit={this.handleSubmit}>
                <FormItem {...formItemLayout} label="">
                    {getFieldDecorator("oldpassword", {
                        rules: [
                            {
                                required: true,
                                message: ""
                            }
                        ]
                    })(<Input type="password" placeholder="" />)}
                </FormItem>
                <FormItem {...formItemLayout} label="">
                    {getFieldDecorator("password", {
                        rules: [
                            {
                                required: true,
                                message: ""
                            },
                            {
                                validator: this.checkPassword
                            }
                        ]
                    })(
                        <Input
                            type="password"
                            onBlur={this.handleConfirmBlur}
                            placeholder=""
                        />
                    )}
                </FormItem>
                <FormItem {...tailFormItemLayout}>
                    <Button
                        style={{
                            marginRight: 10
                        }}
                        onClick={() => {
                            close();
                        }}
                    >
                        
                    </Button>
                    <Button type="primary" htmlType="submit">
                        
                    </Button>
                </FormItem>
            </Form>
        );
    }
}
